import React from 'react';
import PropTypes from 'prop-types';
import { Icon } from 'antd';
import { BrandSectionWrapper } from './styles';

const BrandSection = ({ item, isShowArrow }) => (
  <BrandSectionWrapper>
    <div className="vCenter">
      <Icon className="icInfo" type={item.icon} />
      <div className="txtTitle">
        <span className="txtValue">{item.value}</span> 
        {item.title}
      </div>
      
    </div>
    {isShowArrow && <Icon className="vRight" type="right" />}
  </BrandSectionWrapper>
);
BrandSection.propTypes = {
  item: PropTypes.object,
  isShowArrow: PropTypes.bool,
};

BrandSection.defaultProps = {
  isShowArrow: true,
};

export default BrandSection;
